import React, { useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { setNotification } from '../reducers/notificationReducer'
import TemplatePage from './TemplatePage'
import RegisterForm from '../components/RegisterForm'
import userService from '../services/users'

const RegisterPage = ({ setNotification, history }) => {
  const [registerFormIsVisible, setRegisterFormIsVisible] = useState(true)

  const handleRegister = async (
    event,
    username,
    password,
    setPassword,
    passwordConfirm,
    setPasswordConfirm
  ) => {
    event.preventDefault()
    setPassword('')
    setPasswordConfirm('')

    if (password !== passwordConfirm) {
      setNotification('Salasanat eivt tsm', 'is-warning')
      return
    } else if (!password) {
      setNotification('Salasana ei voi olla tyhj', 'is-warning')
      return
    } else if (username.length < 3) {
      setNotification('Kyttjnimen tulee olla vhintn 3 merkki', 'is-warning')
      return
    }

    try {
      const { user } = await userService.register(username, password)
      setRegisterFormIsVisible(false)
    } catch (error) {
      if (error.response.status === 409) {
        setNotification('Kyttjnimi on jo kytss', 'is-warning')
      } else {
        setNotification('Kyttj ei voitu rekisterid', 'is-danger')
      }
    }
  }

  const registerSection = () => {
    return (
      <>
        <div className="content" style={{ padding: '2em 0' }}>
          <h1 className="title is-3">Rekisteri Tablist kyttj</h1>
          <p className="is-medium">
            Pset kyttmn Tablisti, kun rekisteritymispyyntsi hyvksytn.
          </p>
        </div>
        <RegisterForm handleSubmit={handleRegister} />
      </>
    )
  }

  const successMessage = () => {
    return (
      <article className="message is-primary" style={{ padding: '5em 0' }}>
        <div className="message-header">
          <p>Rekisterinti onnistui!</p>
        </div>
        <div className="message-body">
          Pset kirjautumaan tilille{' '}
          <strong>kun yllpitj on hyvksynyt rekisteritymispyyntsi.</strong>
          <br />
          <br />
          <button className="button is-primary" onClick={() => history.push('/login')}>
            Kirjadu sisn
          </button>
        </div>
      </article>
    )
  }

  return (
    <TemplatePage>
      <div className="container">
        {registerFormIsVisible ? registerSection() : successMessage()}
      </div>
    </TemplatePage>
  )
}

export default connect(
  null,
  { setNotification }
)(withRouter(RegisterPage))
